.div {
  display: flex;
  justify-content: center;

  margin-top: 20px;
  margin-bottom: 20px;
}

.button {
  cursor: pointer;
  padding: 5px 20px 5px 20px;
  border-radius: 8px;
  border: 3px solid rgb(23, 112, 245);
}

.button:hover,
.button:focus {
  background-color: #ffff;
}
